// src/pages/Projects/ProjectDetail.js
import React from 'react';
import { useParams } from 'react-router-dom';
import { projects } from '../data/projects';

const ProjectDetail = () => {
  const { projectId } = useParams(); // URLprojectId
  const project = projects.find(p => p.id === projectId); // ID

  // 
  if (!project) {
    return <div className="container mx-auto py-12 px-4 text-center"></div>;
  }

  // 
  return (
    <div className="container mx-auto py-12 px-4">
      <h1 className="text-4xl font-bold mb-4">{project.title}</h1>
      {/*  */}
      <img src={project.thumbnail} alt={project.title} className="mb-6 rounded shadow-lg" />
      <p className="mb-4">{project.description}</p>
      {/*  ()  */}
    </div>
  );
};

export default ProjectDetail; //  